.Home {
    margin: 0 5%;
}

.Home-Buttons {
    margin-bottom: 20%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.Home-Topic {
    font-size: 7vw;
    text-align: left;
}

.Home-Btn {
    -ms-flex-positive: 2;
        flex-grow: 2;
    -ms-flex-negative: 2;
        flex-shrink: 2;
}

@media only screen and (min-width: 767px) {
    .Home-Btn {
        font-size: 4vw;
        -ms-flex-negative: 0;
            flex-shrink: 0;
        -ms-flex-positive: 1;
            flex-grow: 1;
    }
}

@media only screen and (min-width: 1000px) {
    .Home {
        margin: 0 20%;
    }
    p {
        font-size: 19pt;
    }
    .Home-Topic {
        font-size: 40pt;
    }
    .Home-Btn {
        margin: 2.5% 0;
        font-size: 20pt;
        -ms-flex-negative: 1;
            flex-shrink: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
    }
}

@media only screen and (min-width: 1200px) {
    .Home {
        margin: 0 25%;
        font-size: 20pt;
    }   
    .Home-Topic {
        font-size: 30pt;
        text-align: left;
    }

    .Home p {
        font-size: 18pt;
    }
}


