.BlogLI {
    margin: 2.5% auto;
    overflow: hidden;
    position: relative;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: start;
        align-items: flex-start;

    width: 100%;
    height: 100%;

    word-wrap: normal;
}

.BlogLI-TitleBox {
    width: 60vw;
}

.BlogLI-Title {
    font-family: 'Roboto Condensed', sans-serif;
    font-size: 5vw;
    margin: 0;
}

.BlogLI-Content {

    margin: 0 5vw;
    /* flex-grow: 1; */
    height: 35vw;
    overflow-x: hidden;
    overflow-y: hidden;
    position: relative;
}

.BlogLI-Summary {
    line-height: 1;
}

.BlogLI-Summary::after {
    content: "";
    position:absolute;
    bottom: 0;
    left: 0;
    height: 10vw;
    width:100%;
    z-index: 1;
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(0,0,0,0)), to(rgb(255, 255, 255)));
    background: -webkit-linear-gradient(rgba(0,0,0,0), rgb(255, 255, 255));
    background: -o-linear-gradient(rgba(0,0,0,0), rgb(255, 255, 255));
    background: linear-gradient(rgba(0,0,0,0), rgb(255, 255, 255));
}

.BlogLI-Thumbnail {
    /* set the desired width/height and margin here */
  width: 35vw;
  height: 35vw;

  position: relative;
  overflow: hidden;
  display: inline-block;
  border-radius: 2px;

  -ms-flex-negative: 0;

      flex-shrink: 0;
}

.BlogLI-Thumbnail img {
    position: absolute;
    left: 50%;
    top: 50%;
    height: 100%;
    width: auto;
    -webkit-transform: translate(-50%,-50%);
        -ms-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
}
  

.BlogLI-date {
    font-size: 100%;
    font-style: italic;
    margin: 0;
}

@media only screen and (min-width: 768px) {
    .BlogLI-date {
        font-size: 18pt;
    }
}

@media only screen and (min-width: 1000px) {
    
}

@media only screen and (min-width: 1200px) {

    .BlogLI {
        margin: 5%;
    }

    .BlogLI-Content {
        height: 250px;
    }

    .BlogLI-Thumbnail {
        height: 250px;
        width: 250px;
    }

    .BlogLI-Title {
        font-size: 30pt;
    }

    .BlogLI-Summary {
        font-size: 20pt;
    }
}


