    width: 100%;
    height: 6vh;
    top: 94vh;
    margin: 0;

    background-color: white;
    border-top: solid rgba(0, 0, 0, 0.5) 1px;

    display: -ms-flexbox;

    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;

    font-size: 4vw;
}


@media only screen and (min-width: 1000px) {
    .Footer {
        font-size: 20pt;
    }
}


