.RepoLI {
    margin: 1%;
    margin-bottom: 7.5%;
}

.RepoLI-Dates {
    margin: 3% 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
    font-size: 3vw;
}

.RepoLI-Created, .RepoLI-Updated {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.RepoLI-Time {
    margin: 0 5px;
}

.RepoLI-Dates-Icons {
    font-size: 2.75vh;
    display: inline-block;
}

.RepoLI-Header {
    margin: 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.RepoLI-Title {
    margin: 0;
    font-size: 4.5vw;
}

.RepoLI-Summary {
    margin: 2% auto;
    font-size: 3.5vw;
}

#RepoLI-LinkBtn {
    font-size: 3.5vw;
    color: black;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    padding: 1.5vh;
    height: 4vh;
    margin-left: 1%;
}

.RepoLI-Link-Text {
    display: inline-block;
    margin-right: 5px;
    line-height: 1.2rem;
    margin: 0;
}

@media only screen and (min-width: 1200px) {
    .RepoLI-Time {
       font-size: 15pt;
    }
    .RepoLI-Title {
        font-size: 25pt;
    }
    #RepoLI-LinkBtn {
        font-size: 15pt;
    }
    .RepoLI-Summary {
        font-size: 20pt;
    }
}



